<template>
    <div class="login-container">
        <video id="myVideo" width="320" height="240" controls autoplay ref="video" muted>
            <source src="../../../111111.mp4" type="video/mp4">
            Your browser does not support the video tag.
        </video>
        <button @click="captureFrame">Capture Frame</button>
        <canvas id="canvas"></canvas>
    </div>
</template>

<script lang="ts" setup>
import { onMounted, ref } from 'vue';
const video = ref<any>(null)

function captureFrame() {
    const video = document.getElementById('myVideo') as any;
    const canvas = document.getElementById('canvas') as any;
    const ctx = canvas.getContext('2d');

    // 设置canvas的尺寸与视频一致  
    canvas.width = video.videoWidth;
    canvas.height = video.videoHeight;

    // 绘制视频的当前帧到canvas上  
    ctx.drawImage(video, 0, 0, canvas.width, canvas.height);

    // 将canvas内容转换为图片URL  
    const imageUrl = canvas.toDataURL('image/png');

    // 创建一个链接来下载图片  
    const link = document.createElement('a');
    link.href = imageUrl;
    link.download = 'captured_frame.png';
    link.click();
}

</script>
<style lang="less">
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}
</style>